.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  width: 200px;
  
  &__ph {
    display: flex;
    height: 134px;
    width: 160px;
    margin-bottom: 40px;
  }

  &__title {
    display: flex;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    width: 166px;
    height: 36px;
    box-sizing: border-box;
    color: #060b35;
  }

  &__code {
    display: flex;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    align-self: start;
    margin-top: 4px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    margin-top: 16px;
    align-items: center;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    margin-top: 24px;
  }

  &__button {
    display: flex;
    box-sizing: border-box;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    background: #00acdc;
    border-radius: 5px;
    width: 100%;
    height: 40px;
    text-decoration: none;
    color: #fff;
    margin-top: 16px;

    &:hover {
      display: flex;
      box-sizing: border-box;
      align-items: center;
      justify-content: center;
      text-align: center;
      border: 1px solid #00acdc;
      background: #fff;
      color: #00acdc;
      background-position: center;
      width: 100%;
      margin-top: 16px;
    }
  }
}

.container__card {
  padding: 32px 16px 16px;
  margin-right: $right-card-margin;

  &:nth-last-child(-n + 4) {
    margin-top: 48px;
  }
  
  &:nth-child(4n) {
    margin-right: 0;
  }
}

.rating {
  display: flex;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 14px;
}

.stars {
  display: flex;
  margin: 0;

  &__star {
    display: flex;
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-position: center;
  }
}

.stars--4 .stars__star:nth-child(-n + 4) {
  background-image: url(/images/star-active.svg);
}

.price__text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.price__sum {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}
